<template>
  <div class="base-image" :style="{ width: this.width, height: this.height }">
    <img
      :class="className"
      :style="{ width: this.width, height: this.height }"
      :src="imgSrc"
      v-if="!lazy"
    >
    <img
      :class="className"
      :style="{ width: this.width, height: this.height }"
      v-lazy="imgSrc"
      v-if="lazy"
    >
  </div>
</template>

<script>
const IMG_SRC_HOST = ""
const IMG_STYLES = {
  small: "@!thumb",
  middle: "@!middle",
  max: "@!detail-thumb"
}

export default {
  name: "base-image",
  props: {
    className: String,
    lazy: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '100%'
    },
    height: String,
    src: String,
    zipMode: {
      // zipMode: 'small', 'middle', 'max'
      type: String,
      default: 'middle'
    }
  },
  data() {
    return {
      imgSrc: "I am DemoPage2!"
    }
  },
  created() {
    const src = this.src || ""
    if (src.indexOf("https://") == -1 && src.indexOf("http://") == -1) {
      this.imgSrc = IMG_SRC_HOST + this.src;
    } else {
      this.imgSrc = this.src
    }
    // this.imgSrc += IMG_STYLES[this.zipMode] || "@!middle"
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.base-image {
}
</style>



