export function getIP () {
  return new Promise((resolve, reject) => {
    let script = document.createElement('script')
    script.type = 'text/javascript'
    script.src = 'http://pv.sohu.com/cityjson?ie=utf-8'
    document.getElementsByTagName('head')[0].appendChild(script)
    script.onload = () => {
      resolve(window.returnCitySN.cip)
    }
    script.onerror = (e) => {
      reject(e)
    }
  })
}

export function sendVerifyCode (phone, code) {
  const sign = md5(`${phone}${code}rD1TX0RRAHE56F7nMVKVwKNrh9R1g1h0U6ADZm17j3GJdTHVf0Sjyc4Hq8hNXDg8Z3an4g1ZH1kg3204duJWYy1r2rNR2bYJJ7jQsVQyxrEmsAEnwTgbu0NZ2jZ17ZXe`).toUpperCase()
  const url = `/class/JPClass/Api/sms.ashx?method=SendCode&cellphone=${phone}&checkcode=${code}&sign=${sign}`
  return HttpClient.get(url)
}

export function generateVerifyCode () {
  return Math.random().toString().slice(-6)
}

export function sendOrderInfo (order) {
  return HttpClient.post('/class/JPClass/api/add.ashx', order)
}

/**
 * 支付宝支付接口
 * @param {*} price 课程金额（单位元，接口内会转换为分）
 * @param {*} orderId 订单号
 */
export function alipay (price, orderId) {
  const bodyInfo = {
    body: '精锐教育-50元秋季1对1精品课程购买',
    subject: '课程购买',
    out_trade_no: orderId,
    total_amount: price,
    product_code: 'QUICK_WAP_WAY'
  }
  const payInfo = {
    app_id: '2016101100664069', // TODO: 修改为正式
    method: 'alipay.trade.wap.pay',
    return_url: '',
    charset: 'utf-8',
    sign_type: 'RSA2',
    timestamp: moment().format('YYYY-MM-DD HH:mm:ss'),
    version: '1.0',
    notify_url: '',
    biz_content: JSON.stringify(bodyInfo)
  }

  payInfo.sing = getAlipaySign(payInfo)

  alipayApi('https://openapi.alipaydev.com/gateway.do', payInfo)
}

export function alipayApi (url, parmas) {
  var temp = document.createElement('form')
  temp.action = url
  temp.method = 'post'
  temp.style.display = 'none'

  for (var x in parmas) {
    var opt = document.createElement('textarea')
    opt.name = x
    opt.value = parmas[x]
    temp.appendChild(opt)
  }

  document.body.appendChild(temp)
  temp.submit()
}

