export function initWeixinPay (price, orderId, cip) {
  WX_PAY_BODY.price = price
  WX_PAY_BODY.orderId = orderId
  WX_PAY_BODY.cip = cip

  if (typeof WeixinJSBridge === 'undefined') {
    if (document.addEventListener) {
      document.addEventListener('WeixinJSBridgeReady', payWxOrder, false)
    } else if (document.attachEvent) {
      document.attachEvent('WeixinJSBridgeReady', payWxOrder)
      document.attachEvent('onWeixinJSBridgeReady', payWxOrder)
    }
  } else {
    payWxOrder()
  }
}

export function payWxOrder () {
  const data = {
    'token': '2FA57E46956F716F4AC411F5586096F3EEAE3547032A6F88761A96E05C344CD86E05BEBB5199DD101E3BB02D0694009BA232AF691B7A0F0A99A23AA45CCA84BF',
    'total': 1,
    'h5code': '50元1对1精品课',
    'Ordernum': WX_PAY_BODY.orderId,
    'method': 'PAY'
  }
  HttpClient.postForm('/class/189/api/Pay.ashx', data).then(d => {
    const payData = JSON.parse(d.data.message)
    const payInfo = {
      appId: payData.appId,
      mch_id: '1388040002',
      timeStamp: payData.timeStamp,
      nonceStr: payData.nonceStr,
      body: '精锐教育-50元秋季1对1精品课程购买',
      out_trade_no: WX_PAY_BODY.orderId,
      total_fee: WX_PAY_BODY.price,
      spbill_create_ip: WX_PAY_BODY.cip,
      trade_type: 'JSAPI',
      signType: payData.signType,
      openid: 'oBmFkswEQ86cA4HUyaQrFbpccrsU',
      paySign: payData.paySign,
      package: payData.package,
      notify_url: location.origin + '/notify_url?id=' + WX_PAY_BODY.orderId // TODO: 支付后通知接口，可通过微信访问该页面，然后中cookie方式来告知，或者使用用户主动获取方式
    }
    // eslint-disable-next-line no-undef
    WeixinJSBridge.invoke('getBrandWCPayRequest', payInfo, (res) => {
      alert('res' + JSON.stringify(res))

      if (res.err_msg === 'get_brand_wcpay_request:ok') {
        // 使用以上方式判断前端返回,微信团队郑重提示：
        // res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
        sendOrderInfo()
        location.href = '/pay/result?r=1'
      } else {
        location.href = '/pay/result?r=0'
      }
    })
  })
}

function getAlipaySign (payInfo) {
  let stringSignTemp = ''
  Object.keys(payInfo).sort((a, b) => a - b).forEach((key) => {
    if (payInfo[key] !== '' && payInfo[key] !== undefined && payInfo[key] !== null) {
      stringSignTemp += `${key}=${payInfo[key]}&`
    }
  })
  stringSignTemp = stringSignTemp.substring(stringSignTemp.length - 2, 1)
  return stringSignTemp
}



