<script>
import * as moment from 'moment'
import * as Service from './class-service'
import BaseImage from '@/components/image/base-image'
import BaseSpinner from '@/components/spinner/base-spinner'

function prefixInteger(num, n) {
  return (Array(n).join(0) + num).slice(-n)
}

export default {
  name: 'activity-class-detail',
  components: {
    BaseImage,
    BaseSpinner
  },
  data() {
    return {
      model: null,
      orderId: null,
      cip: null,
      currentGrade: null,
      showDialog: false,
      phone: null,
      verifyCode: null,
      phoneValid: false,
      timer: null,
      countDown: 60,
      clientVerifyCode: null,
      showPayWay: false,
      surplusTime: '仅剩00时00分00秒'
    }
  },
  mounted() {
    let endDate = this.$cookies.get('JingRui-EndTime')
    if (!endDate) {
      endDate = moment().add(48, 'hours').format('YYYY-MM-DD HH:mm:ss')
      this.$cookies.set('JingRui-EndTime', endDate)
    }
    setInterval(()=>{
      let m1 = moment()
      let m2 = moment(endDate)
      var du = moment.duration(m2 - m1, 'ms'),
      hours = du.get('hours'),
      mins = du.get('minutes'),
      ss = du.get('seconds');
      if(hours<=0 && mins<=0 && ss<=0) {
        this.surplusTime = '仅剩00时00分00秒'
        endDate = moment().add(48, 'hours').format('YYYY-MM-DD HH:mm:ss')
        this.$cookies.set('JingRui-EndTime', endDate)
      } else {
        this.surplusTime = `仅剩${prefixInteger(hours,2)}时${prefixInteger(mins,2)}分${prefixInteger(ss,2)}秒`
      }
    },999)
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      Service.getClassDetail().then(d => {
        this.model = d;
        this.currentGrade = d.gradeList[0].grades[0]
        window.title = d.name
      })
      Service.getIP().then(ip => this.cip = ip)
    },
    gotoBuy() {
      if (!this.clientVerifyCode || !this.verifyCode || this.clientVerifyCode !== this.verifyCode) {
        this.$toast('验证码错误')
        return
      }

      if (!this.orderId) {
        this.orderId = moment().format('YYYYMMDDHHmmss') + Math.round(Math.random(0, 1) * 100)
      }

      if (typeof WeixinJSBridge !== 'undefined') {
        this.weixinPay()
        return
      }
      this.showDialog = false
      this.showPayWay = true
    },
    alipay() {
      ap.tradePay({
        tradeNO: this.orderId
      }, function(res) {
        ap.alert('result: '+ JSON.stringify(res))
      })
    },
    weixinPay() {
      Service.initWeixinPay('0.01', this.orderId, this.cip)
    },
    alipay() {
      Service.alipay(0.01, this.orderId)
    },
    clickGrade(grade) {
      this.currentGrade = grade
      this.showDialog = true
    },
    checkMobile() {
      this.phoneValid = /^1[3456789]\d{9}$/.test(this.phone)
      if(!this.phoneValid) {
        this.$toast('手机号填写有误')
      }
    },
    validPhone() {
      this.phoneValid = /^1[3456789]\d{9}$/.test(this.phone)
    },
    getVerifyCode() {
      if (this.phoneValid && this.countDown == 60) {
        this.verifyCode = Service.generateVerifyCode()
        Service.sendVerifyCode(this.phone, this.verifyCode).then(d => {
          this.$toast('验证码已发送')
          this.timer = setInterval(() => {
            if (this.countDown > 0) {
              this.countDown -= 1
            } else {
              this.countDown = 60
              clearInterval(this.timer)
            }
          }, 1000)
        }, e => {
          this.$toast('获取验证码失败')
        })
      }
    },
    postOrderInfo() {
      const order = {
        grade: this.currentGrade.name,
        cellphone: this.phone,
        code: this.orderId,
        channel:"第三方渠道",
        paytype:"微信",
        wxpayno:"",
        alipayno:"",
        amount:"500",
        payresult:"成功",
        paydate: moment().format('YYYY/MM/DD HH:mm:ss'),
        ispaysuccess:"成功"
      }
      Service.sendOrderInfo(order)
    },






  }
}
</script>

