              background:rgba(230,53,53,1);
              border-radius:0.26rem;
              font-size:0.24rem;
              font-family:MicrosoftYaHei;
              font-weight:400;
              color:rgba(255,255,255,1);
              padding: 0.05rem 0.20rem;
            }
          }
          .university {
            font-size:0.24rem;
            font-family:MicrosoftYaHei;
            font-weight:bold;
            color:rgba(34,34,34,1);
            padding-top: 0.14rem;
            padding-bottom: 0.29rem;
          }
        }
      }

      .teacher-ps {
        font-size:0.2rem;
        font-family:MicrosoftYaHeiLight;
        font-weight:400;
        color:rgba(179,179,179,1);
        line-height:0.72rem;
        text-align: center;
      }
    }
  }

  .system {
    .system-title {
      padding: 0.72rem 0rem 0.62rem 0rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .system-info {
      padding: 0rem 0.58rem;
    }
  }

  .care {
    .care-title {
      padding: 0.66rem 0rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .care-info {
      padding: 0rem 0.26rem;

      .question {
        display: flex;
        .question-title {
          background:rgba(230,53,53,1);
          border-radius:0.5rem;
          padding: 0.02rem;
          display: flex;
          align-items: center;

          .question-title-number {
            width:0.56rem;
            height:0.56rem;
            background:rgba(255,255,255,1);
            border-radius: 50%;
            font-size:0.36rem;
            font-family:MicrosoftYaHei;
            font-weight:bold;
            color:rgba(230,53,53,1);
            display: flex;
            align-items: center;
            justify-content: center;
          }

          .question-title-info {
            font-size:0.3rem;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(255,255,255,1);
            line-height:0.3rem;
            padding: 0.15rem;
          }
        }
      }

      .question-answer {
        font-size:0.28rem;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(102,102,102,1);
        line-height:0.28rem;
        padding: 0.49rem 0rem 0.63rem 0.26rem;
        line-height:0.4rem;
      }


    }
  }

  .honor {
    .honor-title {
      padding-top: 0.15rem;
      padding-bottom: 0.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .honor-info {
      display: flex;
      align-items: center;
      overflow-x: scroll;
      overflow-y:hidden;

      .honor-info-pic {
        padding-left: 0.19rem;
        &:first-child {
          padding-left: 0.24rem;
        }
        &:last-child {
          padding-right: 0.24rem;
        }
      }
    }
  }

  .dialog-mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 1;
    background-color: rgba(0,0,0,0.5);
    top: 0;
  }

  .dialog {
    z-index: 2;
    background:rgba(255,255,255,1);
    box-shadow:0px -3px 5px 0px rgba(6,0,1,0.21);
    border-radius:10px 10px 0px 0px;
    position: fixed;
    bottom: 0;
    width: 100vw;

    .dialog-title {
      font-size:0.36rem;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(34,34,34,1);
      line-height:0.4rem;
      padding: 0.32rem 0.29rem;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .text {

      }

      .close, .close img {
        width: 0.46rem;
        height: 0.46rem;
      }
    }

    .dialog-body {
      padding: 0.2rem 0.24rem 0rem 0.24rem;
      background:rgba(248,248,248,1);
      padding-bottom: 0.5rem;


      .dialog-info {
        background:rgba(255,255,255,1);
        border-radius:0.12rem;
        padding: 0.41rem 0.25rem;

        .info-title {
          font-size:0.36rem;
          font-family:MicrosoftYaHei;
          font-weight:bold;
          color:rgba(34,34,34,1);
        }

        .info-content {
          padding: 0.43rem 0.26rem;

          .info-top-info {
            display: flex;
          }


          .info-pic, .info-pic img {
            width: 2rem;
            height: 2rem;
          }

          .info-text {
            padding: 0.16rem 0rem 0.16rem 0.63rem;
            ul {
              li {
                color: rgba(230,53,53,1);
                font-size:0.26rem;
                font-family:MicrosoftYaHei;
                font-weight:400;
                line-height: 0.44rem;

                span {
                  color:rgba(34,34,34,1);
                }
              }
            }
          }

          .info-tips {
            display: flex;

            div {
              background:rgba(230,53,53,0.07);
              border-radius:0.06rem;
              font-size:0.24rem;
              font-family:MicrosoftYaHei;
              font-weight:400;
              color:rgba(230,53,53,1);
              padding: 0.08rem 0.28rem 0.08rem 0.18rem;
              margin-top: 0.28rem;
            }
          }

        }



      }

      .dialog-mobile {
        margin-top: 0.2rem;
        background: #ffffff;
        border-radius:0.12rem;

        .row {
          display: flex;
          padding: 0.32rem 0.24rem;
          align-items: center;

          .label {
            font-size:0.3rem;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(34,34,34,1);
            line-height:0.4rem;
          }

          .text-input {
            font-size:0.3rem;
            font-family:MicrosoftYaHei;
            font-weight:bold;
            margin-left: 0.3rem;
            border: none;

            &::placeholder {
              color: #888888;
              font-weight:normal;
            }


            &:focus {
              border: 0rem;
              outline: none;
            }
          }

          .btn-code {
            background:rgba(204,204,204,1);
            border-radius:0.48rem;
            padding: 0.16rem 0.18rem;
            font-size:0.28rem;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(255,255,255,1);
            line-height:0.28rem;
            margin-left: auto;
            white-space: nowrap;

            &.active {
              background:rgba(230,53,53,1);
              color:rgba(255,255,255,1);
            }
          }
        }
      }

    }


    .pay-info {
      height:1.2rem;
      background:rgba(255,255,255,1);
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 1.2rem;
      padding-left: 0.35rem;
      padding-right: 0.25rem;

      .pay-money {
        font-size:0.36rem;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(34,34,34,1);
        line-height:0.36rem;

        span {
          font-size: 0.6rem;
          color: #E63535;
          font-weight: bold;
          line-height:0.6rem;
          font-family: MicrosoftYaHei-Bold;
        }
      }

      .btn-pay {
        background:rgba(230,53,53,1);
        border-radius:0.48rem;
        padding: 0.25rem 0.52rem;
        font-size:0.36rem;
        font-family:MicrosoftYaHei;
        font-weight:bold;
        color:rgba(255,255,255,1);
        line-height:0.36rem;
      }

    }
  }

  .fixed-bottom {
    position: fixed;
    height:1rem;
    background:rgba(238,238,238,1);
    box-shadow:0rem -0.05rem .25rem 0rem rgba(4,0,0,0.2);
    bottom: 0;
    width: 100%;
    display: inline-flex;
    justify-content:space-between;

    .price-time {
      padding-left: 0.46rem;
      display: flex;
      align-items: center;

      span {
        &:nth-child(1) {
          font-size:0.3rem;
          line-height:0.3rem;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(230,53,53,1);
          padding-top: 0.2rem;
        }

        &:nth-child(2) {
          font-size:0.6rem;
          line-height:0.6rem;
          font-family:MicrosoftYaHei;
          font-weight:bold;
          color:rgba(230,53,53,1);
        }

        &:nth-child(3) {
          font-size:0.3rem;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(34,34,34,1);
          line-height:0.3rem;
          margin-left: 0.23rem;
          padding-top: 0.2rem;
        }
      }
    }

    .btn-buy {
      background:rgba(230,53,53,1);
      height: 100%;
      width:2.12rem;
      font-size:0.36rem;
      font-family:MicrosoftYaHei;
      font-weight:bold;
      color:rgba(255,255,255,1);
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .pay-row {
    display: flex;
    align-items: center;
    padding: .346667rem .4rem .346667rem 0;

    .pay-icon {
      display: inline-block;
      width: .96rem;
      height: .96rem;
      vertical-align: middle;
      background-repeat: no-repeat;

      .pay-icon-svg {
        width: .96rem;
        height: .96rem;
      }
    }

    .pay-name {
      display: inline-block;
      vertical-align: middle;
      line-height: .48rem;
      margin-left: .413333rem;
      font-size: 0.34rem;
    }
  }

/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(90%);
  opacity: 0;
}
</style>



